import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Divider, Button, Drawer, Typography } from 'antd';
import Comments from '../Comments';

class HeaderItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: false,
    };
  }

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };
  render() {
    return (
      <>
        <Row type="flex" justify="end" align="top" offset={1}>
          <Col md={5} sm={11} xs={11}>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontWeight: 'bold',
                fontSize: 14,
                color: '#f17d27',
                marginBottom: '0',
              }}
            >
              {'Patient Name: '}
            </p>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontSize: 14,
                color: 'white',
                marginBottom: '0',
              }}
            >
              {this.props.claim ? (
                this.props.claim.patientFirstName +
                ' ' +
                this.props.claim.patientLastName
              ) : (
                <br />
              )}
            </p>
          </Col>
          <Col md={5} sm={11} xs={11}>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontWeight: 'bold',
                fontSize: 14,
                color: '#f17d27',
                marginBottom: '0',
              }}
            >
              {'UCN:'}{' '}
            </p>
            <p
              style={{
                fontFamily: 'SegoeUI, Segoe UI',
                fontSize: 14,
                color: 'white',
                marginBottom: '0',
              }}
            >
              {this.props.claim ? this.props.claim.id : <br />}
            </p>
          </Col>
        </Row>
        <Row type="flex" justify="end">
          <Col md={10} sm={22} xs={22}>
            <Divider />
          </Col>
        </Row>
        <Row type="flex" justify="end">
          <Button type="primary" onClick={this.showDrawer}>
            Comments
          </Button>
          <Drawer
            title="Comments"
            placement="right"
            closable={false}
            onClose={this.onClose}
            visible={this.state.visible}
            width={'50%'}
          >
            <Comments claim={this.props.claim} />
          </Drawer>
        </Row>
        <Row>
          <Col span={12}>
            <Typography.Title>{this.props.title}</Typography.Title>
          </Col>
        </Row>
      </>
    );
  }
}

export default connect(null, {})(HeaderItem);
