import React from 'react';
import { Empty, Row, Col, Button, Icon } from 'antd';
import FormDocumentsDetails from './documentsDetails';
import basicStyle from '../../settings/basicStyle';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
const SvgButtonComponent = props => (
  <svg width={145} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
);
export default function FormDocuments(props) {
  const { rowStyle } = basicStyle;
  return (
    <div>
      {props.claim &&
      props.claim.ClaimInfoVisits &&
      props.claim.ClaimInfoVisits.length > 0 ? (
        props.claim.ClaimInfoVisits.map(visit => (
          <FormDocumentsDetails
            key={visit.id}
            visit={visit}
            setClick={click => {
              this.clickChild[visit.id] = click;
            }}
            claimUpdateHandler={props.claimUpdateHandler}
          />
        ))
      ) : (
        <Empty description={<span>No Visit entered</span>} />
      )}

      <Row style={rowStyle} type="flex" justify="end">
        <Col>
          <Button
            type="link"
            htmlType="submit"
            onClick={() => {
              SuperFetch.put('claim/claim', {
                id: props.claim.id,
                status: 'pe',
              })
                .then(response => {
                  if (response.status === 200) {
                    if (props.match.params.id) {
                      props.history.goBack();
                    } else {
                      window.location.reload(false);
                    }
                  } else {
                    notification(
                      'error',
                      'Something wrong in updating Billing Infomation'
                    );
                  }
                })
                .catch(e => console.log(e));
            }}
          >
            <Icon component={SvgButtonComponent} />
          </Button>
        </Col>
      </Row>
      <Row style={rowStyle}>
        <svg width={949} height={5}>
          <path fill="none" stroke="#f17d27" strokeWidth={5} d="M0 2.5h949" />
        </svg>
      </Row>
    </div>
  );
}
