import React from 'react';
import basicStyle from '../../settings/basicStyle';
import { Row, Col, Collapse, Upload, Button, Table } from 'antd';
import moment from 'moment';
import BenefitsLabelData from './BenefitLabelDataInit';
import UploadStyleWrapper from './upload.style';
import UploadImg from './UploadButtonBill.png';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import FormBillingInfoDetailsSub from './billingInfoDetailsSub';
import FileDownloader from '../../helpers/fileDownloader';

const TitleSvgComponent = props => (
  <svg {...props} width={236} height={33}>
    <text
      data-name="Visit_1(12 Jan 2020):"
      transform="translate(0 27)"
      fill="#f17d27"
      fontSize={25}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        Visit {props.name}
      </tspan>
      <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
        {props.date ? props.date : null}
      </tspan>
    </text>
  </svg>
);

export default function BillingInfoDetails(props) {
  const { rowStyle } = basicStyle;
  const customPanelStyle = {
    background: '#0F305E',
    paddingBottom: 24,
    border: 0,
    overflow: 'hidden',
  };
  const columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
      render: (text, record) => {
        console.log(record);
        return (
          <Button
            type="link"
            onClick={e => {
              FileDownloader(record.url, record.name);
            }}
          >
            {text}
          </Button>
        );
      },
    },
    {
      title: 'Action',
      key: 'action',
      render: (text, file) => (
        <div className="isoInvoiceBtnView">
          <Button
            icon="download"
            // icon="delete"
            onClick={() => {
              console.log(file);
              FileDownloader(file.url, file.name);
            }}
          ></Button>
          <Button
            icon="delete"
            // icon="delete"
            onClick={() => {
              SuperFetch.delete('claim/billingInfoFiles', { id: file.id })
                .then(response => {
                  var newBillingInfoFiles = this.state.BillingInfoFiles;
                  var index = newBillingInfoFiles.findIndex(
                    item => item.id === response.data.id
                  );
                  //https://www.codementor.io/lautiamkok/javascript-tips-searching-the-index-of-an-element-and-delete-or-replace-it-cfpy6uasy
                  newBillingInfoFiles.splice(index, 1);

                  this.setState({ BillingInfoFiles: newBillingInfoFiles });
                })
                .catch(e => console.log(e));
            }}
          ></Button>
        </div>
      ),
    },
  ];
  return (
    <div style={rowStyle}>
      <Row type="flex" justify="space-between" name="currency" style={rowStyle}>
        <Col>
          <TitleSvgComponent
            date={
              props.visit.dateOfAdmissionVisit == null
                ? null
                : moment(props.visit.dateOfAdmissionVisit).format('DD MMM YYYY')
            }
          />
        </Col>
      </Row>
      <Row name="data" style={rowStyle}>
        <Collapse bordered={false}>
          {new BenefitsLabelData().getAll().map(category => (
            <Collapse.Panel
              showArrow={false}
              header={
                <Row>
                  <Col span={12}>
                    <span style={{ fontWeight: 'bold', color: '#0F305E' }}>
                      {category.name}
                    </span>
                  </Col>
                  <Col span={6}>{props.visit.billingCurrency}</Col>
                  <Col span={6}>USD</Col>
                </Row>
              }
              key={category.id}
              style={customPanelStyle}
            >
              <Row>
                <FormBillingInfoDetailsSub
                  category={category}
                  billingInfosSub={props.visit.BillingInfos.filter(
                    item => item.billingCat === category.id
                  )}
                  visit={props.visit}
                  visitIndex={props.visitIndex}
                  claimUpdateHandler={props.claimUpdateHandler}
                />
              </Row>
            </Collapse.Panel>
          ))}
        </Collapse>
      </Row>
      <Row name="tabledata" style={rowStyle}>
        {props.visit.BillingInfoFiles.length > 0 && (
          <Table
            bordered={false}
            rowKey="id"
            dataSource={props.visit.BillingInfoFiles}
            columns={columns}
            pagination={false}
          />
        )}
      </Row>
      <UploadStyleWrapper name="uploadForm" style={{ width: 300 }}>
        <Upload
          action="/api/claim/upload"
          headers={{
            Authorization: localStorage.getItem('id_token') || undefined,
          }}
          onChange={info => {
            switch (info.file.status) {
              case 'done':
                const newData = {
                  visitId: props.visit.id,
                  url: info.file.response.path,
                  name: info.file.name,
                };
                //notification('success', "File uploaded" );
                SuperFetch.post('claim/insertBillingInfoFiles', newData)
                  .then(response => {
                    if (response.status === 200) {
                      props.claimUpdateHandler();
                    } else {
                      notification(
                        'error',
                        'Something wrong in retrieving data'
                      );
                    }
                  })
                  .catch(e => console.log(e));

                break;
              default:
                break;
            }
          }}
        >
          <img src={UploadImg} alt="UploadImg" />
        </Upload>
      </UploadStyleWrapper>
    </div>
  );
}
