import React, { useState } from 'react';
import { Table, Row, Col, Input } from 'antd';
import { constant } from '../../settings';
import { EditableFormRow, EditableCell } from './CustomizedCellsNumber';

export default function BillingInfo(props) {
  var initTableData = props.category.BenefitSubCategories.map(subCat => {
    subCat.BillingInfo = props.billingInfosSub.find(
      bill => bill.billingSubCat === subCat.id
    )
      ? props.billingInfosSub.find(bill => bill.billingSubCat === subCat.id)
          .value
      : null;
    return subCat;
  });
  const [tableData] = useState(initTableData);
  const components = {
    body: {
      row: EditableFormRow,
      cell: EditableCell,
    },
  };

  const saveOther = e => {
    console.log(props.visit);
    console.log(e);
  };

  const columnsOri = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
      width: '45%',
      render: (text, record) => {
        return record.id === 39 ? (
          <Input
            //ref={node => (this.inputOther = node)}
            onPressEnter={saveOther}
            onBlur={saveOther}
            //defaultValue={this.props.otherName}
          />
        ) : (
          text
        );
      },
    },
    {
      title: 'BillingInfo',
      dataIndex: 'BillingInfo',
      editable: true,
      width: '25%',
    },
    {
      title: 'USD',
      dataIndex: 'BillingInfo',
      key: 'USD',
      render: (text, record) => {
        return text ? text.toFixed(constant.fixedDigital) : 0;
      },
      width: '25%',
    },
  ];

  const columns = columnsOri.map(col => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: (record, index) => ({
        record,
        index,
        value: (record[col.dataIndex] * props.visit.billingRate).toFixed(
          constant.fixedDigital
        ),
        editable: col.editable,
        dataIndex: col.dataIndex,
        title: col.title,
        visit: props.visit,
        claimUpdateHandler: props.claimUpdateHandler,
      }),
    };
  });

  return (
    <Table
      components={components}
      pagination={{ position: 'none', pageSize: tableData.length }}
      dataSource={tableData}
      columns={columns}
      rowKey="id"
      showHeader={false}
      footer={() => (
        <Row type="flex" justify="end">
          <Col span={6}>
            <span>{'Amounts Claimed: '}</span>
          </Col>
          <Col span={6}>
            <span>
              {tableData.length > 0
                ? (
                    tableData.reduce((acc, curr) => acc + curr.BillingInfo, 0) *
                    props.visit.billingRate
                  ).toFixed(constant.fixedDigital)
                : 0}
            </span>
          </Col>
          <Col span={6}>
            <span>
              {tableData.length > 0
                ? tableData
                    .reduce((acc, curr) => acc + curr.BillingInfo, 0)
                    .toFixed(constant.fixedDigital)
                : 0}
            </span>
          </Col>
        </Row>
      )}
    />
  );
}
