import React, { useState, useRef } from 'react';
import { CustomizedForm } from './claimInfoForm';
import { Row, Col, Button, Icon, Table } from 'antd';
import VisitForm from './visitForm';
import moment from 'moment';
import basicStyle from '../../settings/basicStyle';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';

const SvgButtonComponent = props => (
  <svg width={142.415} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
);
//https://stackblitz.com/edit/react-a1mlqw
export default function FormClaimInfo(props) {
  // Declare a new state variable, which we'll call "count"
  const [visible, setVisible] = useState(false);
  const [visit, setVisit] = useState(null);
  const refs = useRef([]);

  const showModal = () => {
    setVisit(null);
    setVisible(true);
  };

  const handleCancel = () => {
    setVisit(null);
    setVisible(false);
    refs.current.resetFields();
  };
  const columns = [
    {
      title: 'Date of Admission/Visit',
      dataIndex: 'dateOfAdmissionVisit',
      key: 'dateOfAdmissionVisit',
      render: (text, visit) => (
        <Button
          type="link"
          onClick={e => {
            setVisible(true);
            setVisit(visit);
            // this.setState({ visible: true, visit: visit }, () => {
            //   this.child.setVisit(); // do stuff
            // });
          }}
        >
          {moment(text).format('DD MMM YYYY')}
        </Button>
      ),
    },
    {
      title: 'Hospital or Clinic Name',
      dataIndex: 'hospitalOrClinicName',
      key: 'hospitalOrClinicName',
    },
    {
      title: 'Medical Diagnosis',
      key: 'MedicalDiagnosis',
      dataIndex: 'MedicalDiagnosis',
    },
    {
      title: 'Billing Currency',
      key: 'billingCurrency',
      dataIndex: 'billingCurrency',
    },
    {
      title: 'Hospitl or Clinic Country',
      key: 'hospitalOrClinicCountry',
      dataIndex: 'hospitalOrClinicCountry',
    },
  ];

  const { rowStyle } = basicStyle;
  return (
    <div>
      <CustomizedForm {...props} />
      {props.claim ? (
        <>
          <Row style={rowStyle}>
            <Button type="primary" onClick={showModal}>
              New Visit
            </Button>
          </Row>

          <VisitForm
            //wrappedComponentRef={this.saveFormRef}
            visible={visible}
            onCancel={handleCancel}
            onCreate={props.visitHandler}
            visit={visit}
            ref={refs}
          />
          <Row style={rowStyle}>
            <Table
              columns={columns}
              dataSource={props.claim.ClaimInfoVisits}
              rowKey="id"
              style={rowStyle}
              pagination={false}
              width={'100%'}
            />
          </Row>

          <Row
            style={{ paddingTop: 50, paddingBottom: 20 }}
            type="flex"
            justify="end"
          >
            <Col>
              <Button
                type="link"
                htmlType="submit"
                onClick={async () => {
                  //After saving or updating data && sending out email && get exchange rate
                  //console.log(props.match.url.split('/')[2]);
                  if (props.match.url.split('/')[2] === 'register') {
                    let rateRes = await SuperFetch.put('claim/rate', {
                      id: props.claim.id,
                    });
                    let emailRes = await SuperFetch.post('email/send', {
                      claimID: props.claim.id,
                    });
                    console.log('emailRes', emailRes);
                    if (
                      rateRes.status === 200 &&
                      emailRes.status === 200 &&
                      emailRes.data.rejected.length < 1
                    ) {
                      notification(
                        'success',
                        'Capturing exchange rate and sending email successfully!'
                      );
                      props.indexHandler('BillingButton');
                    } else if (emailRes.data.rejected.length > 0) {
                      let emails = emailRes.data.rejected.reduce(
                        (acc, curr) => acc + ';' + curr,
                        ''
                      );
                      notification('error', 'Emails fail:' + emails);
                    } else {
                      notification('error', 'Something wrong happens');
                    }
                  } else {
                    let rateRes = await SuperFetch.put('claim/rate', {
                      id: props.claim.id,
                    });
                    if (rateRes.status === 200) {
                      notification(
                        'success',
                        'Capturing exchange rate successfully!'
                      );
                      props.indexHandler('BillingButton');
                    } else {
                      notification('error', 'Something wrong happens');
                    }
                  }
                }}
              >
                <Icon component={SvgButtonComponent} />
              </Button>
            </Col>
          </Row>
          <Row>
            <svg width={949} height={5}>
              <path
                fill="none"
                stroke="#f17d27"
                strokeWidth={5}
                d="M0 2.5h949"
              />
            </svg>
          </Row>
        </>
      ) : null}
    </div>
  );
}
