import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Row, Col } from 'antd';
import FormClaimInfo from './claimInfo';
import FormBillingInfo from './billingInfo';
import FormDocuments from './documents';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import base64url from 'base64url';
import HeaderItem from '../HeaderItem';
import jwtDecode from 'jwt-decode';
//https://github.com/ayox/antd-form-item-view-hoc

function Notification(props) {
  switch (props.tabIndex) {
    case 'ClaimsButton':
      return <FormClaimInfo {...props} />;
    case 'BillingButton':
      return <FormBillingInfo {...props} />;
    case 'DocumentsButton':
      return <FormDocuments {...props} />;
    default:
      return <FormClaimInfo {...props} />;
  }
}
function Claim(props) {
  const [tabIndex, setTabIndex] = useState('ClaimsButton');
  const [claimIDurl, setClaimIDurl] = useState(
    props.match ? props.match.params.id : base64url(props.claim.claimID)
  );
  const [claim, setClaim] = useState(null);

  useEffect(() => {
    const claimUpdateHandler2 = () => {
      if (claimIDurl)
        SuperFetch.get('claim/claim', { id: claimIDurl })
          .then(response => {
            if (response.status === 200) {
              setClaim(response.data);
            } else {
              notification('error', 'Something wrong in retrieving claim');
            }
          })
          .catch(e => console.log(e));
      else {
        setClaim(null);
      }
    };

    claimUpdateHandler2();
  }, [claimIDurl]);
  const onClick = e => {
    setTabIndex(e.currentTarget.id);
  };
  const indexHandler = e => {
    setTabIndex(e);
    claimUpdateHandler();
  };
  const claimInfoHandler = claimValue => {
    claimValue.createdBy = jwtDecode(props.idToken).id;
    claimValue.status = 'pr';
    if (claim) {
      claimValue.id = claim.id;
      SuperFetch.put('claim/claim', claimValue)
        .then(response => {
          if (response.status === 200) {
            claimUpdateHandler();
          } else {
            notification('error', 'Something wrong in saving claim');
          }
        })
        .catch(e => console.log(e));
    } else {
      SuperFetch.post('claim/claim', claimValue)
        .then(response => {
          if (response.status === 200) {
            setClaimIDurl(base64url(response.data.id));
            // setClaimIDurl(base64url(response.data.id))
            // claimUpdateHandler();
          } else {
            notification('error', 'Something wrong in saving claim');
          }
        })
        .catch(e => console.log(e));
    }
  };
  const visitHandler = visit => {
    if (visit.id) {
      SuperFetch.put('claim/visit', visit)
        .then(response => {
          if (response.status === 200) {
            claimUpdateHandler();
          } else {
            notification('error', 'Something wrong in saving claim');
          }
        })
        .catch(e => console.log(e));
    } else {
      visit.claimInfoId = claim.id;
      SuperFetch.post('claim/visit', visit)
        .then(response => {
          if (response.status === 200) {
            claimUpdateHandler();
          } else {
            notification('error', 'Something wrong in saving claim');
          }
        })
        .catch(e => console.log(e));
    }

    // claim.ClaimInfoVisits = claim.ClaimInfoVisits ? claim.ClaimInfoVisits : [];
    // visit.visitNumber = claim.ClaimInfoVisits.length + 1;
    // claim.ClaimInfoVisits.push(visit);
    // console.log('visitHandler', visit);
  };

  const claimUpdateHandler = () => {
    if (claimIDurl)
      SuperFetch.get('claim/claim', { id: claimIDurl })
        .then(response => {
          if (response.status === 200) {
            notification('success', 'Claim Info Updated Successfully!');
            setClaim(response.data);
          } else {
            notification('error', 'Something wrong in retrieving claim');
          }
        })
        .catch(e => console.log(e));
    else {
      setClaim(null);
    }
  };

  return (
    <Col span={18} offset={2}>
      <HeaderItem title="Register Claim" claim={claim} />
      <Row>
        <Col md={15} sm={22} xs={22}>
          <svg width={'100%'} preserveAspectRatio="none" viewBox="0 0 955 52">
            <defs>
              <linearGradient
                id="linearGradient_tabs"
                x1={0.5}
                y1={1}
                x2={0.5}
                gradientUnits="objectBoundingBox"
              >
                <stop offset={0} stopColor="#25325a" />
                <stop offset={1} stopColor="#5b6fa3" />
              </linearGradient>
            </defs>
            <g id="TabButtons" transform="translate(0 -76)">
              <g id="DocumentsButton" onClick={onClick}>
                <g
                  id="documents_container"
                  transform="translate(563 76)"
                  fill={
                    tabIndex === 'DocumentsButton'
                      ? '#f17d27'
                      : 'url(#linearGradient_tabs)'
                  }
                >
                  <path
                    d="M 368.9072875976563 50.5 L 10.82559967041016 50.5 C 8.219449996948242 50.5 5.790200233459473 49.85725021362305 3.985249996185303 48.69015121459961 C 3.057100057601929 48.09000015258789 1.5 46.8125 1.5 45 L 1.5 7 C 1.5 6.222000122070313 1.822849988937378 4.708049774169922 3.985249996185303 3.309849977493286 C 5.790200233459473 2.142750024795532 8.219449996948242 1.5 10.82559967041016 1.5 L 368.9503479003906 1.5 L 390.0201110839844 24.80078887939453 L 368.9072875976563 50.5 Z"
                    stroke="none"
                  />
                  <path
                    d="M 368.1983032226563 49 L 388.040283203125 24.84774017333984 L 368.284423828125 3 L 10.8255786895752 3 C 8.538578987121582 3 6.342210292816162 3.572052717208862 4.799684047698975 4.569473743438721 C 3.65594744682312 5.309052467346191 3 6.194947242736816 3 7 L 3 45 C 3 45.8050537109375 3.65594744682312 46.69094848632813 4.799684047698975 47.43052673339844 C 6.342210292816162 48.42794799804688 8.538578987121582 49 10.8255786895752 49 L 368.1983032226563 49 M 369.6162719726563 52 L 10.8255786895752 52 C 4.846789360046387 52 0 48.86600112915039 0 45 L 0 7 C 0 3.134000062942505 4.846789360046387 0 10.8255786895752 0 L 369.6162719726563 0 L 392 24.75384140014648 L 369.6162719726563 52 Z"
                    stroke="none"
                    fill="#0b314a"
                  />
                </g>
                <text
                  id="DOCUMENTS"
                  transform="translate(713 109)"
                  fill={tabIndex === 'DocumentsButton' ? '#202c54' : '#fff'}
                  fontSize="20"
                  fontFamily="SegoeUI-Semibold, Segoe UI"
                  fontWeight="600"
                  letterSpacing="0.02em"
                >
                  <tspan x="-62.625" y="0">
                    DOCUMENTS
                  </tspan>
                </text>
              </g>
              <g id="BillingButton" onClick={onClick}>
                <g
                  id="billing_info_container"
                  transform="translate(247.272 76)"
                  fill={
                    tabIndex === 'BillingButton'
                      ? '#f17d27'
                      : 'url(#linearGradient_tabs)'
                  }
                >
                  <path
                    d="M 363.8228759765625 50.5 L 10.67687892913818 50.5 C 8.113779067993164 50.5 5.724678993225098 49.85910034179688 3.949628829956055 48.6953010559082 C 2.369978904724121 47.65964889526367 1.500028848648071 46.3473014831543 1.500028848648071 45 L 1.500028848648071 7 C 1.500028848648071 5.652699947357178 2.369978904724121 4.340350151062012 3.949628829956055 3.304699897766113 C 5.724678993225098 2.140899896621704 8.113779067993164 1.5 10.67687892913818 1.5 L 363.8659057617188 1.5 L 384.6459350585938 24.80045890808105 L 363.8228759765625 50.5 Z"
                    stroke="none"
                  />
                  <path
                    d="M 363.1076965332031 49 L 382.6776428222656 24.84701347351074 L 363.19384765625 3 L 10.67687892913818 3 C 8.435579299926758 3 6.283329010009766 3.568249940872192 4.772078990936279 4.559100151062012 C 4.239278793334961 4.908450126647949 3.000028848648071 5.845550060272217 3.000028848648071 7 L 3.000028848648071 45 C 3.000028848648071 46.15444946289063 4.239278793334961 47.09154891967773 4.772078990936279 47.44089889526367 C 6.283329010009766 48.4317512512207 8.435579299926758 49 10.67687892913818 49 L 363.1076965332031 49 M 364.5380859375 52 L 10.67687892913818 52 C 4.780179023742676 52 2.890625000873115e-05 48.86600112915039 2.890625000873115e-05 45 L 2.890625000873115e-05 7 C 2.890625000873115e-05 3.134000062942505 4.780179023742676 0 10.67687892913818 0 L 364.5380859375 0 L 386.6142883300781 24.75385093688965 L 364.5380859375 52 Z"
                    stroke="none"
                    fill="#0b314a"
                  />
                </g>
                <text
                  id="BILLING_INFO"
                  data-name="BILLING INFO"
                  transform="translate(392 109)"
                  fill={tabIndex === 'BillingButton' ? '#202c54' : '#fff'}
                  fontSize="20"
                  fontFamily="SegoeUI-Semibold, Segoe UI"
                  fontWeight="600"
                  letterSpacing="0.02em"
                >
                  <tspan x="-64.407" y="0">
                    BILLING INFO
                  </tspan>
                </text>
              </g>
              <g id="ClaimsButton" onClick={onClick}>
                <g
                  id="claim_info_container"
                  transform="translate(0 76)"
                  fill={
                    tabIndex === 'ClaimsButton'
                      ? '#f17d27'
                      : 'url(#linearGradient_tabs)'
                  }
                >
                  <path
                    d="M 293.119384765625 50.5 L 8.608847618103027 50.5 C 6.65297269821167 50.5 4.827180862426758 49.89125061035156 3.467806100845337 48.78591537475586 C 2.198847770690918 47.75412368774414 1.500014305114746 46.40958404541016 1.500014305114746 45 L 1.500014305114746 7 C 1.500014305114746 5.590416431427002 2.198847770690918 4.245874881744385 3.467806100845337 3.214083433151245 C 4.827180862426758 2.108750104904175 6.65297269821167 1.5 8.608847618103027 1.5 L 293.1622314453125 1.5 L 309.9131164550781 24.79449844360352 L 293.119384765625 50.5 Z"
                    stroke="none"
                  />
                  <path
                    d="M 292.3075866699219 49 L 308.0947875976563 24.83517456054688 L 292.3932800292969 3 L 8.608847618103027 3 C 5.56855583190918 3 3.000014305114746 4.831791877746582 3.000014305114746 7 L 3.000014305114746 45 C 3.000014305114746 47.16820907592773 5.56855583190918 49 8.608847618103027 49 L 292.3075866699219 49 M 293.93115234375 52 L 8.608847618103027 52 C 3.854347705841064 52 1.432291628589155e-05 48.86600112915039 1.432291628589155e-05 45 L 1.432291628589155e-05 7 C 1.432291628589155e-05 3.134000062942505 3.854347705841064 3.552713678800501e-15 8.608847618103027 3.552713678800501e-15 L 293.93115234375 3.552713678800501e-15 L 311.7314453125 24.75383377075195 L 293.93115234375 52 Z"
                    stroke="none"
                    fill="#0b314a"
                  />
                </g>
                <text
                  id="CLAIMS_INFO"
                  data-name="CLAIMS INFO"
                  transform="translate(82 109)"
                  fill={tabIndex === 'ClaimsButton' ? '#202c54' : '#fff'}
                  fontSize="20"
                  fontFamily="SegoeUI-Semibold, Segoe UI"
                  fontWeight="600"
                  letterSpacing="0.02em"
                >
                  <tspan x="-63.592" y="0">
                    CLAIMS INFO
                  </tspan>
                </text>
              </g>
            </g>
          </svg>
        </Col>
      </Row>
      <Notification
        tabIndex={tabIndex}
        params={claimIDurl}
        history={props.history}
        claim={claim}
        claimInfoHandler={claimInfoHandler}
        visitHandler={visitHandler}
        indexHandler={indexHandler}
        claimUpdateHandler={claimUpdateHandler}
        match={props.match}
      />
    </Col>
  );
}

export default connect(state => ({ idToken: state.Auth.idToken }), {})(Claim);
