import React, { useEffect, useState } from 'react';
import {
  Modal,
  Form,
  Input,
  DatePicker,
  Select,
  Upload,
  Button,
  Table,
  Col,
  Row,
} from 'antd';
import UploadImg from './UploadButton.png';
import * as countries from 'i18n-iso-countries';
import currencyData from './currencyData';
import moment from 'moment';
import FileDownloader from '../../helpers/fileDownloader';
const { Option } = Select;
//https://stackoverflow.com/questions/58037115/react-16-call-childrens-function-from-parent-when-using-hooks-and-functional-c
const VisitForm = React.forwardRef((props, ref) => {
  const [claimInfoVisitsFiles, setClaimInfoVisitsFiles] = useState([]);
  const { getFieldDecorator } = props.form;
  countries.registerLocale(require('i18n-iso-countries/langs/en.json'));

  // useImperativeHandle(ref, () => ({
  //   focus: () => {
  //     console.log('inputRef.current.focus()');
  //   }
  // }));

  //fill visit Form
  useEffect(() => {
    if (props.visit) {
      let {
        hospitalOrClinicName,
        hospitalOrClinicCountryrl,
        hospitalOrclinicEmail,
        MedicalDiagnosis,
        dateOfAdmissionVisit,
        hospitalOrClinicCountry,
        doctorName,
        billingCurrency,
      } = props.visit;
      dateOfAdmissionVisit = dateOfAdmissionVisit
        ? moment(dateOfAdmissionVisit)
        : null;
      props.form.setFieldsValue({
        hospitalOrClinicName,
        hospitalOrClinicCountryrl,
        hospitalOrclinicEmail,
        MedicalDiagnosis,
        dateOfAdmissionVisit,
        hospitalOrClinicCountry,
        doctorName,
        billingCurrency,
      });
    } else {
      props.form.resetFields();
      handleChange([]);
    }
  }, [props.visit]);

  const columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Action',
      key: 'action',
      render: (text, file) => {
        return (
          <div className="isoInvoiceBtnView">
            <Button
              icon="download"
              // icon="delete"
              onClick={() => {
                console.log(file);
                FileDownloader(file.url, file.name);
              }}
            ></Button>
          </div>
        );
      },
    },
  ];
  const handleChange = ({ fileList }) => setClaimInfoVisitsFiles(fileList);
  return (
    <Modal
      visible={props.visible}
      title="Create a new visit"
      okText="Create"
      footer={[
        <Button key="cancel" type="primary" onClick={props.onCancel}>
          Cancel
        </Button>,
        <Button
          key="submit"
          type="primary"
          onClick={() => {
            props.form.validateFieldsAndScroll(async (err, visit) => {
              if (!err) {
                if (props.visit) {
                  visit.id = props.visit.id;
                }

                //format ClaimInfoVisitsFiles for db
                if (Array.isArray(claimInfoVisitsFiles)) {
                  visit.ClaimInfoVisitsFiles = claimInfoVisitsFiles.map(
                    file => {
                      file.response.url = file.response.path;
                      return file.response;
                    }
                  );
                }
                //console.log(claimInfoVisitsFiles)
                await props.onCreate(visit);
                props.onCancel();
              }
            });
          }}
        >
          Submit
        </Button>,
      ]}
    >
      <Form>
        <Row>
          <Col span={10}>
            <Form.Item
              colon={false}
              label={<span>Hospital or Clinic Name:</span>}
            >
              {getFieldDecorator('hospitalOrClinicName', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Hospital or Clinic Name',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item
              colon={false}
              label={<span>Hospital or Clinic url:</span>}
            >
              {getFieldDecorator('hospitalOrClinicCountryrl', {
                rules: [],
              })(<Input />)}
            </Form.Item>
            <Form.Item
              colon={false}
              label={<span>Hospital or Clinic Email:</span>}
            >
              {getFieldDecorator('hospitalOrclinicEmail', {
                rules: [],
              })(<Input />)}
            </Form.Item>
            <Form.Item colon={false} label={<span>Medical Diagnosis:</span>}>
              {getFieldDecorator('MedicalDiagnosis', {
                rules: [],
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={10} offset={2}>
            <Form.Item
              colon={false}
              label={<span>Date of Admission/Visit:</span>}
            >
              {getFieldDecorator('dateOfAdmissionVisit', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Date of Admission/Visit',
                  },
                ],
              })(
                <DatePicker 
                style={{ width: '100%' }} 
                format={'DD MMM YYYY'} 
                />
              )}
            </Form.Item>
            <Form.Item
              colon={false}
              label={<span>Hospital or Clinic Country:</span>}
            >
              {getFieldDecorator('hospitalOrClinicCountry', {
                rules: [],
              })(
                <Select
                  style={{ width: '100%' }}
                  placeholder="Select country..."
                  showSearch
                  filterOption={(input, option) => {
                    return (
                      option.props.children
                        .toLowerCase()
                        .indexOf(input.toLowerCase()) >= 0 ||
                      option.props.value
                        .toLowerCase()
                        .indexOf(input.toLowerCase()) >= 0
                    );
                  }}
                >
                  {Object.entries(countries.getNames('en')).map(
                    ([code, name]) => (
                      <Option key={code} value={code}>
                        {name}
                      </Option>
                    )
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item colon={false} label={<span>Doctor Name:</span>}>
              {getFieldDecorator('doctorName', {
                rules: [],
              })(<Input />)}
            </Form.Item>

            <Form.Item colon={false} label={<span>Billing Currency:</span>}>
              {getFieldDecorator('billingCurrency', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Billing Currency',
                  },
                ],
              })(
                <Select
                  placeholder="Select or enter..."
                  showSearch
                  filterOption={(input, option) => {
                    return (
                      option.props.children[0]
                        .toLowerCase()
                        .indexOf(input.toLowerCase()) >= 0 ||
                      option.props.children[2]
                        .toLowerCase()
                        .indexOf(input.toLowerCase()) >= 0
                    );
                  }}
                >
                  {currencyData
                    ? currencyData.map(currency => (
                        <Select.Option
                          value={currency.value}
                          key={currency.value}
                        >
                          {currency.label} - {currency.value}
                        </Select.Option>
                      ))
                    : null}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        {props.visit && props.visit.ClaimInfoVisitsFiles ? (
          <Table
            columns={columns}
            dataSource={props.visit.ClaimInfoVisitsFiles}
            rowKey="id"
            width="100%"
          />
        ) : null}

        <Upload
          action="/api/claim/upload"
          headers={{
            Authorization: localStorage.getItem('id_token') || undefined,
          }}
          onChange={handleChange}
          fileList={claimInfoVisitsFiles}
        >
          <img src={UploadImg} alt="UploadImg" />
        </Upload>
      </Form>
    </Modal>
  );
});

export default Form.create()(VisitForm);
