import React from 'react';
import {
  Row,
  Col,
  Form,
  Input,
  Select,
  DatePicker,
  Checkbox,
  InputNumber,
  Button,
} from 'antd';
import basicStyle from '../../settings/basicStyle';
import currencyData from './currencyData';
import moment from 'moment';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
export let CustomizedForm = Form.create({
  name: 'global_state',
  onFieldsChange(props, changedFields) {},
  mapPropsToFields(props) {
    return props.claim
      ? {
          policyFirstName: Form.createFormField({
            ...props.policyFirstName,
            value: props.claim.policyFirstName,
          }),
          policyLastName: Form.createFormField({
            ...props.policyLastName,
            value: props.claim.policyLastName,
          }),
          policyEmail: Form.createFormField({
            ...props.policyEmail,
            value: props.claim.policyEmail,
          }),
          policyNumber: Form.createFormField({
            ...props.policyNumber,
            value: props.claim.policyNumber,
          }),
          policyType: Form.createFormField({
            ...props.policyType,
            value: props.claim.policyType,
          }),
          policyVip: Form.createFormField({
            ...props.policyVip,
            value: props.claim.policyVip,
          }),
          policyGroupPolicy: Form.createFormField({
            ...props.policyGroupPolicy,
            value: props.claim.policyGroupPolicy,
          }),
          patientFirstName: Form.createFormField({
            ...props.patientFirstName,
            value: props.claim.patientFirstName,
          }),
          patientLastName: Form.createFormField({
            ...props.patientLastName,
            value: props.claim.patientLastName,
          }),
          patientDob: Form.createFormField({
            ...props.patientDob,
            value: props.claim.patientDob
              ? moment(props.claim.patientDob)
              : null,
          }),
          patientAge: Form.createFormField({
            ...props.patientAge,
            value: props.claim.patientAge,
          }),
          contactFirstName: Form.createFormField({
            ...props.contactFirstName,
            value: props.claim.contactFirstName,
          }),
          contactLastName: Form.createFormField({
            ...props.contactLastName,
            value: props.claim.contactLastName,
          }),
          contactEmail: Form.createFormField({
            ...props.contactEmail,
            value: props.claim.contactEmail,
          }),
          contactHomeAddress: Form.createFormField({
            ...props.contactHomeAddress,
            value: props.claim.contactHomeAddress,
          }),
          contactPhoneNumberCountryCode: Form.createFormField({
            ...props.contactPhoneNumberCountryCode,
            value: props.claim.contactPhoneNumberCountryCode,
          }),
          contactPhoneNumber: Form.createFormField({
            ...props.contactPhoneNumber,
            value: props.claim.contactPhoneNumber,
          }),
          relationshipToPatient: Form.createFormField({
            ...props.relationshipToPatient,
            value: props.claim.relationshipToPatient,
          }),
          accountHoldersName: Form.createFormField({
            ...props.accountHoldersName,
            value: props.claim.accountHoldersName,
          }),
          bankName: Form.createFormField({
            ...props.bankName,
            value: props.claim.bankName,
          }),
          bankAddress: Form.createFormField({
            ...props.bankAddress,
            value: props.claim.bankAddress,
          }),
          reimbusementCurrency: Form.createFormField({
            ...props.reimbusementCurrency,
            value: props.claim.reimbusementCurrency,
          }),
          bankAccountNumber: Form.createFormField({
            ...props.bankAccountNumber,
            value: props.claim.bankAccountNumber,
          }),
          ibanCodeSortCode: Form.createFormField({
            ...props.ibanCodeSortCode,
            value: props.claim.ibanCodeSortCode,
          }),
          swift: Form.createFormField({
            ...props.swift,
            value: props.claim.swift,
          }),
          cause: Form.createFormField({
            ...props.cause,
            value: props.claim.cause,
          }),
          gop: Form.createFormField({
            ...props.gop,
            value: props.claim.gop,
          }),
        }
      : null;
  },
  // onValuesChange(_, values) {
  //     console.log(values);
  // },
})(props => {
  const { getFieldDecorator } = props.form;
  const { Option } = Select;
  const { rowStyle } = basicStyle;
  const formItemstyling = {
    marginRight: '25px',
  };
  const formItemFirstColstyling = {
    md: { span: 10 },
    xs: { span: 22 },
  };
  const formItemSecondColstyling = {
    md: { span: 10 },
    xs: { span: 22 },
  };
  const formItemThirdColstyling = {
    md: { span: 4 },
    xs: { span: 22 },
  };
  const labelstyling = {
    color: 'white',
  };

  const copyContact = e => {
    e.preventDefault();
    props.form.setFieldsValue({
      contactFirstName: props.form.getFieldValue('policyFirstName'),
      contactLastName: props.form.getFieldValue('policyLastName'),
      contactEmail: props.form.getFieldValue('policyEmail'),
    });
  };

  const copyContactToPatient = e => {
    e.preventDefault();
    props.form.setFieldsValue({
      patientFirstName: props.form.getFieldValue('policyFirstName'),
      patientLastName: props.form.getFieldValue('policyLastName'),
    });
  };

  return (
    <Form onSubmit={e => e.preventDefault()}>
      <Row name="policyTitle">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="218"
          height="40"
          viewBox="0 0 218 40"
        >
          <text
            id="_1._Policy_Details"
            transform="translate(0 32)"
            fill="#fff"
            fontSize="30"
            fontFamily="SegoeUI-Semibold, Segoe UI"
            fontWeight="600"
          >
            <tspan x="0" y="0">
              1. Policy Details
            </tspan>
          </text>
        </svg>
      </Row>
      <Row name="policyholderForm" style={rowStyle}>
        <Col {...formItemFirstColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Policyholder First Name:</span>}
          >
            {getFieldDecorator('policyFirstName', {
              rules: [
                {
                  required: true,
                  message: 'Please input Policyholder First Name',
                },
              ],
            })(<Input placeholder="Daniel" />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Policyholder Last Name:</span>}
          >
            {getFieldDecorator('policyLastName', {
              rules: [
                {
                  required: true,
                  message: 'Please input Policyholder Last Name',
                },
              ],
            })(<Input placeholder="Smith" />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Policyholder Email:</span>}
          >
            {getFieldDecorator('policyEmail', {
              rules: [],
            })(<Input />)}
          </Form.Item>
        </Col>
        <Col {...formItemSecondColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Policy Number:</span>}
          >
            {getFieldDecorator('policyNumber', {
              rules: [
                {
                  required: true,
                  message: 'Please input Policy Number',
                },
              ],
            })(
              <Input.Search
                placeholder="RIH/YYYY/XX/11111111"
                onSearch={value => {
                  if (
                    ['TLS', 'LI', 'MM', 'ST', 'CO', 'FC'].includes(
                      value.split('/')[2]
                    )
                  ) {
                    props.form.setFieldsValue({
                      policyType: value.split('/')[2],
                    });
                  }
                  SuperFetch.get('claim/firstClaim', { policyNumber: value })
                    .then(response => {
                      if (response.status === 200) {
                        console.log(response);
                      } else {
                        notification(
                          'error',
                          'Something wrong in retrieving claim'
                        );
                      }
                    })
                    .catch(e => console.log(e));
                }}
                enterButton
              />
            )}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Plan Type:</span>}
          >
            {getFieldDecorator('policyType', {
              rules: [
                {
                  required: true,
                  message: 'Please input Reimbusement Currency',
                },
              ],
            })(
              <Select
                style={{ width: '100%' }}
                placeholder="Auto populate or select..."
              >
                <Option value="TLS">Thailand Long Stay</Option>
                <Option value="LI">Life Insurance</Option>
                <Option value="MM">Major Medical</Option>
                <Option value="ST">Standard</Option>
                <Option value="CO">Comprehensive</Option>
                <Option value="FC">Fully Comprehensive</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col {...formItemThirdColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>VIP:</span>}
          >
            {getFieldDecorator('policyVip', {
              initialValue: false,
              valuePropName: 'checked',
            })(<Checkbox></Checkbox>)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Group Policy:</span>}
          >
            {getFieldDecorator('policyGroupPolicy', {
              initialValue: false,
              valuePropName: 'checked',
            })(<Checkbox></Checkbox>)}
          </Form.Item>
        </Col>
      </Row>
      <Row name="patientForm" style={rowStyle}>
        <Col {...formItemFirstColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Patient First Name:</span>}
          >
            {getFieldDecorator('patientFirstName', {
              rules: [],
            })(<Input />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Patient Last Name:</span>}
          >
            {getFieldDecorator('patientLastName', {
              rules: [],
            })(<Input />)}
          </Form.Item>
        </Col>
        <Col {...formItemSecondColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Patient DOB:</span>}
          >
            {getFieldDecorator('patientDob', {
              rules: [],
            })(
            <DatePicker 
            style={{ width: '100%' }} 
            format={'DD MMM YYYY'} 
            disabledDate={current  => {
              return current && current > moment().endOf('day');
            }}
            />)}
          </Form.Item>
          <Form.Item colon={false} label={<span style={labelstyling}></span>}>
            <Button type="primary" onClick={copyContactToPatient}>
              Copy from Policyholder
            </Button>
          </Form.Item>
        </Col>
        <Col {...formItemThirdColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Patient Age:</span>}
          >
            {getFieldDecorator('patientAge', {
              rules: [],
            })(<InputNumber />)}
          </Form.Item>
        </Col>
      </Row>
      <Row name="contactForm" style={rowStyle}>
        <Col {...formItemFirstColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Contact First Name:</span>}
          >
            {getFieldDecorator('contactFirstName', {
              rules: [],
            })(<Input />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Contact Last Name:</span>}
          >
            {getFieldDecorator('contactLastName', {
              rules: [],
            })(<Input />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Contact Email:</span>}
          >
            {getFieldDecorator('contactEmail', {
              rules: [
                {
                  required: true,
                  message: 'Please input Contact Email',
                },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Contact Home Address:</span>}
          >
            {getFieldDecorator('contactHomeAddress', {
              rules: [],
            })(<Input.TextArea rows={4} />)}
          </Form.Item>
        </Col>
        <Col {...formItemSecondColstyling}>
          <Row>
            <Col span={5}>
              <Form.Item
                colon={false}
                style={formItemstyling}
                label={<span style={labelstyling}>Contact Phone Number:</span>}
              >
                {getFieldDecorator('contactPhoneNumberCountryCode', {
                  rules: [],
                })(<Input placeholder="+000" />)}
              </Form.Item>
            </Col>
            <Col span={19}>
              <Form.Item
                colon={false}
                style={formItemstyling}
                label={<span style={labelstyling}></span>}
              >
                {getFieldDecorator('contactPhoneNumber', {
                  rules: [],
                })(<Input placeholder="000000000000" />)}
              </Form.Item>
            </Col>
          </Row>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Relationship to Patient:</span>}
          >
            {getFieldDecorator('relationshipToPatient', {
              rules: [],
            })(<Input />)}
          </Form.Item>
          <Form.Item colon={false} label={<span style={labelstyling}></span>}>
            <Button type="primary" onClick={copyContact}>
              Copy from Policyholder
            </Button>
          </Form.Item>
        </Col>
      </Row>
      <Row name="AccountForm" style={rowStyle}>
        <Col {...formItemFirstColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Account Holder Name:</span>}
          >
            {getFieldDecorator('accountHoldersName', {
              rules: [],
            })(<Input />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Bank Name:</span>}
          >
            {getFieldDecorator('bankName', {
              rules: [],
            })(<Input />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Bank Address:</span>}
          >
            {getFieldDecorator('bankAddress', {
              rules: [],
            })(<Input.TextArea rows={6} />)}
          </Form.Item>
        </Col>
        <Col {...formItemSecondColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Reimbusement Currency:</span>}
          >
            {getFieldDecorator('reimbusementCurrency', {
              rules: [
                {
                  required: true,
                  message: 'Please input Reimbusement Currency',
                },
              ],
            })(
              <Select
                style={{ width: '100%' }}
                placeholder="Auto populate or select..."
                showSearch
                filterOption={(input, option) => {
                  return (
                    option.props.children[0]
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0 ||
                    option.props.children[2]
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  );
                }}
              >
                {currencyData.map(currency => (
                  <Option value={currency.value} key={currency.value}>
                    {currency.label} - {currency.value}
                  </Option>
                ))}
              </Select>
            )}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Bank Account Number:</span>}
          >
            {getFieldDecorator('bankAccountNumber', {
              rules: [],
            })(<Input placeholder="00000000000000" />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>IBAN Code/Sort Code:</span>}
          >
            {getFieldDecorator('ibanCodeSortCode', {
              rules: [],
            })(<Input placeholder="00000000000000" />)}
          </Form.Item>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>SWIFT:</span>}
          >
            {getFieldDecorator('swift', {
              rules: [],
            })(<Input placeholder="00000000" />)}
          </Form.Item>
        </Col>
      </Row>

      <Row name="causeForm" style={rowStyle}>
        <Col md={18} sm={22} xs={22}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>Cause:</span>}
          >
            {getFieldDecorator('cause', {
              rules: [],
            })(<Input.TextArea rows={4} />)}
          </Form.Item>
        </Col>
        <Col {...formItemThirdColstyling}>
          <Form.Item
            colon={false}
            style={formItemstyling}
            label={<span style={labelstyling}>GOP:</span>}
          >
            {getFieldDecorator('gop', {
              initialValue: false,
              valuePropName: 'checked',
            })(<Checkbox></Checkbox>)}
          </Form.Item>
        </Col>
      </Row>
      <Form.Item>
        <Button
          type="primary"
          onClick={e => {
            props.form.validateFieldsAndScroll(async (err, claimInfo) => {
              if (!err) props.claimInfoHandler(claimInfo);
            });
          }}
        >
          Save Claim
        </Button>
      </Form.Item>
      <Row name="Divider">
        <svg width={948} height={1}>
          <path fill="none" stroke="#fff" d="M0 .5h948" />
        </svg>
      </Row>
    </Form>
  );
});
