import React from 'react';
import { InputNumber, Form } from 'antd';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import { constant } from '../../settings';
const EditableContext = React.createContext();

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
  state = {
    editing: true,
  };

  toggleEdit = () => {
    const editing = !this.state.editing;
    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  save = e => {
    if (!this.props.visit.billingRate) {
      notification(
        'error',
        'Please go back Claim Info click Submit to get Exchange Rate'
      );
      return false;
    }
    let bill = {
      visitId: this.props.visit.id,
      billingCat: this.props.record.categoryId,
      billingSubCat: this.props.record.id,
      value: (e.currentTarget.value / this.props.visit.billingRate).toFixed(
        constant.fixedDigital
      ),
    };
    console.log(e.currentTarget.value);
    console.log(this.props.visit.billingRate);
    console.log(bill);
    SuperFetch.post('claim/uploadBillingInfo', bill)
      .then(response => {
        if (response.status === 200) {
          this.props.claimUpdateHandler();
        } else {
          notification(
            'error',
            'Something wrong in updating Billing Infomation'
          );
        }
      })
      .catch(e => console.log(e));
  };

  renderCell = form => {
    this.form = form;
    const { children, dataIndex, value, title } = this.props;
    const { editing } = this.state;
    return editing ? (
      <Form.Item style={{ margin: 10 }}>
        {form.getFieldDecorator(dataIndex, {
          rules: [
            {
              required: true,
              message: `${title} is required.`,
            },
          ],
          initialValue: value,
        })(
          <InputNumber
            id={1}
            ref={node => (this.input = node)}
            onPressEnter={this.save}
            onBlur={this.save}
            min={0}
          />
        )}
      </Form.Item>
    ) : (
      <div
        className="editable-cell-value-wrap"
        style={{ paddingRight: 24 }}
        onClick={this.toggleEdit}
      >
        {children}
      </div>
    );
  };

  render() {
    const {
      editable,
      dataIndex,
      title,
      index,
      handleSave,
      children,
      claimUpdateHandler,
      ...restProps
    } = this.props;
    return (
      <td {...restProps}>
        {editable ? (
          <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
        ) : (
          children
        )}
      </td>
    );
  }
}

export { EditableContext, EditableRow, EditableFormRow, EditableCell };
