import React from 'react';
import FormBillingInfoDetails from './billingInfoDetails';
import { Empty, Row, Col, Button, Icon } from 'antd';
const SvgButtonComponent = props => (
  <svg width={145} height={52}>
    <path
      d="M3.494 0h115.811l23.11 27.246L119.305 52H3.494C1.564 52 0 48.866 0 45V7c0-3.866 1.564-7 3.494-7z"
      fill="#f17d27"
    />
    <text
      transform="translate(61.415 33)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
      letterSpacing=".02em"
    >
      <tspan x={-37.191} y={0}>
        {'SUBMIT'}
      </tspan>
    </text>
  </svg>
);

const SvgComponent = props => (
  <svg width={665} height={76} {...props}>
    <defs>
      <linearGradient
        id="prefix__a"
        x1={0.5}
        y1={1}
        x2={0.5}
        gradientUnits="objectBoundingBox"
      >
        <stop offset={0} stopColor="#25325a" />
        <stop offset={1} stopColor="#5b6fa3" />
      </linearGradient>
    </defs>
    <path
      transform="translate(0 33)"
      fill="url(#prefix__a)"
      d="M0 0h665v43H0z"
    />
    <text
      transform="translate(253 62)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
    >
      <tspan x={-215.723} y={0}>
        {'Total Amounts Claimed:'}
      </tspan>
    </text>
    <text
      data-name="0,000,000.00"
      transform="translate(555 62)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        {props.total.toFixed(2)}
      </tspan>
    </text>

    <text
      transform="translate(593 23)"
      fill="#fff"
      fontSize={20}
      fontFamily="SegoeUI-Semibold, Segoe UI"
      fontWeight={600}
    >
      <tspan x={-19.648} y={0}>
        {'USD'}
      </tspan>
    </text>
  </svg>
);
export default function BillingInfo(props) {
  return (
    <div>
      {props.claim &&
      props.claim.ClaimInfoVisits &&
      props.claim.ClaimInfoVisits.length > 0 ? (
        props.claim.ClaimInfoVisits.map((visit, visitIndex) => (
          <FormBillingInfoDetails
            key={visit.id}
            visit={visit}
            claimUpdateHandler={props.claimUpdateHandler}
            visitIndex={visitIndex}
          />
        ))
      ) : (
        <Empty description={<span>No Visit entered</span>} />
      )}
      <Row
        style={{ paddingTop: 50, paddingBottom: 20 }}
        type="flex"
        justify="end"
      >
        <SvgComponent
          total={
            props.claim &&
            props.claim.ClaimInfoVisits &&
            props.claim.ClaimInfoVisits.length > 0
              ? props.claim.ClaimInfoVisits.reduce(
                  (acc, curr) =>
                    acc +
                    curr.BillingInfos.reduce(
                      (billingAcc, billingCurr) =>
                        billingAcc + billingCurr.value,
                      0
                    ),
                  0
                )
              : 0
          }
        />
      </Row>
      <Row
        style={{ paddingTop: 50, paddingBottom: 20 }}
        type="flex"
        justify="end"
      >
        <Col>
          <Button
            type="link"
            htmlType="submit"
            onClick={() => {
              props.indexHandler('DocumentsButton');
            }}
          >
            <Icon component={SvgButtonComponent} />
          </Button>
        </Col>
      </Row>
      <Row>
        <svg width={949} height={5}>
          <path fill="none" stroke="#f17d27" strokeWidth={5} d="M0 2.5h949" />
        </svg>
      </Row>
    </div>
  );
}
