import React from 'react';
import { Row, Upload, Table, Button } from 'antd';
import basicStyle from '../../settings/basicStyle';
import moment from 'moment';
import FileDownloader from '../../helpers/fileDownloader';
import UploadImg from './UploadButton.png';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';

import {
  EditableFormRow,
  EditableCell,
} from '@isomorphic/shared/isomorphic/components/Tables/CustomizedCells';
const TitleSvgComponent = props => (
  <svg {...props} width={236} height={33}>
    <text
      data-name="Visit_1(12 Jan 2020):"
      transform="translate(0 27)"
      fill="#f17d27"
      fontSize={25}
      fontFamily="SegoeUI-Bold, Segoe UI"
      fontWeight={700}
    >
      <tspan x={0} y={0}>
        Visit {props.name}
      </tspan>
      <tspan y={0} fontFamily="SegoeUI, Segoe UI" fontWeight={400}>
        {props.date}
      </tspan>
    </text>
  </svg>
);
export default function FormDocumentsDetails(props) {
  const { rowStyle } = basicStyle;

  const columns1 = [
    {
      title: 'Documents',
      dataIndex: 'name',
      width: '30%',
      render: (text, record) => {
        return (
          <Button
            type="link"
            onClick={e => {
              FileDownloader(record.url, record.name);
            }}
          >
            <span style={{ color: '#1890ff' }}>{text}</span>
          </Button>
        );
      },
    },
    {
      title: 'Notes',
      dataIndex: 'notes',
      editable: true,
    },
  ];

  const columns = columns1.map(col => {
    if (!col.editable) {
      return col;
    }
    return {
      ...col,
      onCell: record => ({
        record,
        editable: col.editable,
        dataIndex: col.dataIndex,
        title: col.title,
        handleSave: handleSave,
      }),
    };
  });
  const components = {
    body: {
      row: EditableFormRow,
      cell: EditableCell,
    },
  };

  const handleSave = row => {
    SuperFetch.post('claim/insertdocumentsnotes', row)
      .then(response => {
        if (response.status === 200) {
          props.claimUpdateHandler();
        } else {
          notification(
            'error',
            'Something wrong in updating Billing Infomation'
          );
        }
      })
      .catch(e => console.log(e));
    //this.props.insertDocuments({ id: row.id, url: row.url, notes: row.notes });
  };
  return (
    <div style={rowStyle}>
      <Row type="flex" justify="space-between" name="currency" style={rowStyle}>
        <TitleSvgComponent
          date={
            props.visit.dateOfAdmissionVisit == null
              ? null
              : moment(props.visit.dateOfAdmissionVisit).format('DD MMM YYYY')
          }
        />
      </Row>
      <Row name="tabledata" style={rowStyle}>
        {props.visit.DocumentsFiles.length > 0 && (
          <Table
            bordered={false}
            components={components}
            rowClassName={() => 'editable-row'}
            dataSource={props.visit.DocumentsFiles}
            columns={columns}
            pagination={false}
            className={'someruame'}
            rowKey="id"
            style={{ width: '100%' }}
          />
        )}
      </Row>
      <Row name="upload" style={{ width: 300, rowStyle }}>
        <Upload
          action="/api/claim/uploaddocument"
          headers={{
            Authorization: localStorage.getItem('id_token') || undefined,
          }}
          data={{ visitId: props.visit.id }}
          showUploadList={false}
          onChange={info => {
            switch (info.file.status) {
              case 'done':
                props.claimUpdateHandler();
                break;
              case 0:
                break;
              default:
                break;
            }
          }}
        >
          <img src={UploadImg} alt="UploadImg" />
        </Upload>
        ,
      </Row>
    </div>
  );
}
